<template>
  <div class="mobile-header__top" />
  <div class="mobile-header__wrap" :class="{ 'mobile-header__menu-shown': showMenu }">
    <Container variant="header" background-color="white" box-shadow radius="0" class="mobile-header">
      <Icon
        :name="showMenu ? 'grey-close' : 'menu-stack'"
        class="mobile-header__menu-btn"
        @click="toggleMenu"
      />
      <Icon
        class="mobile-header__logo"
        name="logo-icon"
        width="98px"
        height="16px"
        @click="logoClick"
      />
      <Lang dropdownSide="bottom" class="mobile-header__lang" />
    </Container>
    <Container v-if="showMenu" radius="0" background-color="white" class="mobile-header__menu">
      <Text
        v-for="item in menuTools"
        :key="item.key"
        variant="a"
        weight="500"
        @click="goTo(item)"
      >
        {{ item.title }}
      </Text>
    </Container>
    <LoaderBtn v-show ="isLoading" class="mobile-header__loader"/>
  </div>
</template>

<script>
import { MODULES_NAMES } from '@/common/modules'
import { MAIN_ROUTES_NAMES } from '@/common/routes'
import { useI18n } from 'vue-i18n'
import { useRouter } from 'vue-router'
import { useGetters } from '@/plugins/vuex'
import { ref, computed } from 'vue'
import Icon from '@/components/Icon/Icon'
import Lang from '@/components/UIKit/Lang'
import Text from '@/components/Text/Text'
import LoaderBtn from '@/components/UIKit/Loader/LoaderBtn'
import Container from '@/components/Container'

export default {
  name: 'MobileHeader',
  components: {
    Text,
    Lang,
    Icon,
    Container,
    LoaderBtn
  },
  setup () {
    const { t } = useI18n()
    const router = useRouter()
    const {
      getIsLoading: isLoading
    } = useGetters(MODULES_NAMES.LOADING)

    const showMenu = ref(false)
    const menuTools = computed(() => [
      { title: t('footer.fees'), key: 'news', to: MAIN_ROUTES_NAMES.FEE_SCHEDULE },
      { title: t('footer.news'), key: 'news', to: MAIN_ROUTES_NAMES.NEWS },
      { title: t('footer.aboutUs'), key: 'about', to: MAIN_ROUTES_NAMES.ABOUT_US },
      { title: 'AML/KYC', key: 'faq', to: MAIN_ROUTES_NAMES.FAQ }
    ])

    const logoClick = () => {
      router.push({ name: MAIN_ROUTES_NAMES.HOME })
    }
    const toggleMenu = () => {
      showMenu.value = !showMenu.value
    }
    const goTo = (item) => {
      if (item.to) {
        router.push({ name: item.to })
        showMenu.value = false
      }
    }
    return {
      t,
      showMenu,
      isLoading,
      menuTools,
      goTo,
      logoClick,
      toggleMenu
    }
  }
}
</script>

