  &__logo {
    margin-top: 18px;
    margin-bottom: 19px;
  }
  &__menu-btn {
    margin-left: 20px;
  }
  &__lang {
    margin-right: 9px;
  }
  &__menu-shown {
    overflow: hidden;
    box-shadow: 0 1px 5px rgba(0, 0, 0, 0.05), 0 2px 60px rgba(0, 0, 0, 0.3);
